<template>
  <div class="list">
    <v-text-field
      name="name"
      label="SEARCH"
      id="id"
      v-model="title"
    ></v-text-field>


    <v-row>
      <v-col lg="6">
        <v-card>
          <div v-if="tutorials.length > 0">
            <v-list>
              <v-list-item-group v-model="currentIndex">
                <v-list-item
                  v-for="(tutorial, i) in tutorials"
                  :key="i"
                  @click="setTutorial(tutorial)"
                >
                  <v-list-item-content dark>
                    {{ tutorial.title }}</v-list-item-content
                  >
                </v-list-item>
              </v-list-item-group>
            </v-list>
            <div align="center"><v-btn color="error">Remove</v-btn></div>
          </div>

          <div v-else>
            <p>Data doesn't exit</p>
          </div>
        </v-card>
      </v-col>
      <v-col lg="6">
        <h4>Tutorials</h4>
        <div v-if="currentTutorial">
          <div>
            <label for=""><strong>Title:</strong></label
            >{{ currentTutorial.title }}
          </div>
          <div>
            <label for=""><strong>Description</strong></label
            >{{ currentTutorial.description }}
          </div>
          <div>
            <label for=""><strong>Status:</strong></label
            >{{ currentTutorial.published ? "published" : "pending" }}
          </div>
        </div>
        <div v-else>
          <p>Please selet a tutorial</p>
        </div>
        <br />
        <v-btn color="error" depressed>EDIT</v-btn>
      </v-col>
    </v-row>
  </div>
</template>


<script>
import TutorialDataService from "../services/TutorialDataService";
export default {
  name: "TutorialsList",

  data() {
    return {
      tutorials: [],
      currentTutorial: null,
      currentIndex: -1,
      title: "",
    };
  },
  methods: {
    retrieveTutorials() {
      TutorialDataService.getAll()
        .then((res) => {
          this.tutorials = res.data;
          console.log(this.tutorials);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    setTutorial(tutorial) {
      this.currentTutorial = tutorial;
    },
  },
  watch: {
    title: function(val) {
      TutorialDataService.findByTitle(val)
        .then((res) => {
          this.tutorials = res.data;
          console.log(res.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.retrieveTutorials();
  },
};
</script>


<style scoped>
.v-list-item-group .v-list-item--active {
  background-color: black;
  color: white;
}
.list {
  max-width: 800px;
  margin: auto;
}
</style>